<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <el-table :data="items">
      <!-- 
                      el:
                      :data for
                      prop
                      scope.row()slot-scope='scope'
                -->
      <el-table-column prop="_id" label="ID" width="230"></el-table-column>
      <el-table-column prop="parent.name" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <!--  this.$route router this.$router router -->
          <!-- 
                                  el:
                                  scope.row()
                                  $router.push(url)/
                                  
                            -->
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/categories/edit/${scope.row._id}`)"
            ></el-button
          >

          <el-button type="primary" size="small" @click="remove(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    }
  },
  methods: {
    //
    async fetch() {
      const res = await this.$http.get('rest/categories')
      this.items = res.data
    },
    async remove(row) {
      this.$confirm(`${row.name}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(async () => {
        // 
        await this.$http.delete(`rest/categories/${row._id}`)
        this.$message({
          type: 'success',
          message: '!',
        })
        //
        this.fetch()
      })
    },
  },
  created() {
    this.fetch()
  },
  components: {},
}
</script>

<style scoped lang="scss"></style>
