<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <el-form @submit.native.prevent="save" label-width="120px">
      <el-form-item label="">
        <el-select v-model="model.parent">
          <el-option
            v-for="item in parentOptions"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.number"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <img v-if="model.icon" :src="model.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterAudioUpload"
          :on-progress="uploadVideoProcess"
        >
          <audio
            width="200px"
            height="200px"
            :src="model.audio"
            v-if="model.audio != '' && videoFlag == false"
            controls="controls"
          >
            
          </audio>
          <i
            v-else-if="model.audio == '' && videoFlag == false"
            class="el-icon-plus avatar-uploader-icon"
          ></i>
          <el-progress
            v-if="videoFlag == true"
            type="circle"
            :percentage="videoUploadPercent"
            style="margin-top:30px;"
          ></el-progress>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //
    id: { type: String },
  },
  data() {
    return {
      parentOptions: [],
      model: {},
      videoFlag: true,
      videoUploadPercent: 0,
    }
  },
  created() {
    this.id && this.fetch()
    this.fetchParentOptions()
  },
  methods: {
    async fetchParentOptions() {
      //id
      const res = await this.$http.get(`rest/categories`)
      //:  this.model.name = res.data.name 
      this.parentOptions = res.data
    },
    uploadVideoProcess(event, file, fileList) {
      this.videoFlag = true
      this.videoUploadPercent = Math.floor(event.percent)
    },
    afterUpload(res) {
      // :this.modelvue 1vue$set(,key,value)2
      // this.model.icon=res.url vue
      this.$set(this.model, 'icon', res.url)
    },
    afterAudioUpload(res) {
      // :this.modelvue 1vue$set(,key,value)2
      // this.model.icon=res.url vue

      this.videoFlag = false
      this.videoUploadPercent = 0
      if (res.url) {
        this.$set(this.model, 'audio', res.url)
      } else {
        this.$message.error('')
      }
    },
    async fetch() {
      //id
      const res = await this.$http.get(`rest/audios/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = res.data
      console.log(this.model)
      if (this.model.audio) {
        this.videoFlag = false
      }
    },
    async save() {
      if (this.id) {
        await this.$http.put(`rest/audios/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/audios', this.model)
      }
      this.$router.push('/audios/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
  },
  components: {},
}
</script>
