<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
    <el-form @submit.native.prevent="save">
      <el-form-item label="">
        <!-- 
                  
               -->
        <el-select v-model="model.parent">
          <!-- 
                    item.name
                    item._id
                 -->
          <el-option
            v-for="item in parentOptions"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //
    id: { type: String },
  },
  data() {
    return {
      model: {},
      parentOptions: [],
    }
  },
  created() {
    /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.id && this.fetch() //=> this.id true this.fetch
    this.fetchParentOptions()
  },
  methods: {
    async fetch() {
      //id
      const res = await this.$http.get(`rest/categories/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = res.data
      console.log(this.model)
    },
    async save() {
      if (this.id) {
        await this.$http.put(`rest/categories/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/categories', this.model)
      }
      this.$router.push('/categories/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
    async fetchParentOptions() {
      //id
      const res = await this.$http.get(`rest/categories`)
      //:  this.model.name = res.data.name 
      this.parentOptions = res.data
    },
  },
  components: {},
}
</script>

<style scoped lang="scss"></style>
