<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
    <el-form @submit.native.prevent="save" label-width="120px">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <!-- 
                  el:
                  action="" 
                  :on-success 
                  :before-upload 
                  $http.defaults.baseURL //$http.defaults
              -->
        <!-- 
                el::action
                elaxios
                token
               -->
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <img v-if="model.icon" :src="model.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //
    id: { type: String },
  },
  data() {
    return {
      model: {},
    }
  },
  created() {
    /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.id && this.fetch() //=> this.id true this.fetch
  },
  methods: {
    afterUpload(res) {
      // :this.modelvue 1vue$set(,key,value)2
      // this.model.icon=res.url vue
      this.$set(this.model, 'icon', res.url)
    },
    async fetch() {
      //id
      const res = await this.$http.get(`rest/items/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = res.data
      console.log(this.model)
    },
    async save() {
      if (this.id) {
        await this.$http.put(`rest/items/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/items', this.model)
      }
      this.$router.push('/items/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
  },
  components: {},
}
</script>
