<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button type="default" size="small" @click="model.items.push({})"
          ><i class="el-icon-plus"></i></el-button
        >
        <el-row type="flex" style="flex-wrap:wrap">
          <!-- 
              element:
              24
             -->
          <el-col :md="24" v-for="(item, i) in model.items" :key="i">
            <el-form-item label="URL">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-top:0.5rem">
              <!-- 
                       item.icon=res.urlvuedataitemiconitem.iconvue:
                       $set(item,'icon',res.url)
                     -->
              <el-upload
                class="avatar-uploader"
                :action="$http.defaults.baseURL + '/upload'"
                :headers="getAuthHeaders()"
                :show-file-list="false"
                :on-success="(res) => $set(item, 'image', res.url)"
              >
                <img v-if="item.image" :src="item.image" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button
                type="danger"
                size="small"
                @click="model.items.splice(i, 1)"
                ></el-button
              >
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //
    id: { type: String },
  },
  data() {
    return {
      model: {
        items: [],
      },
    }
  },
  created() {
    /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.id && this.fetch() //=> this.id true this.fetch
  },
  methods: {
    async fetch() {
      //id
      const res = await this.$http.get(`rest/ads/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = Object.assign({}, this.model, res.data)
      console.log(this.model)
    },
    async save() {
      if (this.id) {
        await this.$http.put(`rest/ads/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/ads', this.model)
      }
      this.$router.push('/ads/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
  },
  components: {},
}
</script>

<style scoped lang="scss"></style>
