<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
    <el-form label-width="120px" @submit.native.prevent="save">
      <!-- <markdown-editor v-model="content" /> -->
      <el-form-item label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="model.created_at"
          type="datetime"
          placeholder=""
          align="right"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="model.needLoginGithub">
          <el-option
            v-for="item in needLoginGithub"
            :key="item"
            :label="item.name"
            :value="item"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="model.html_url"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button type="default" size="small" @click="model.labels.push({})"
          ><i class="el-icon-plus"></i></el-button
        >
        <el-row type="flex" style="flex-wrap:wrap">
          <el-col :md="24" v-for="(item, i) in model.labels" :key="i">
            <el-form-item label="" class="mt5">
              <el-input v-model="item.name"></el-input>
            </el-form-item>
            <el-form-item label="URL" class="mt5">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="height:40px" class="mt5">
              <el-color-picker v-model="item.color"></el-color-picker>
            </el-form-item>
            <el-form-item label="" class="mt5">
              <el-button
                type="danger"
                icon="el-icon-delete"
                circle
                @click="delete_item(i)"
              ></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item label="">
        <markdown-editor v-model="model.body" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import MarkdownEditor from '../components/MarkdownEditor'
export default {
  props: {
    //
    id: { type: String },
  },
  components: {
    MarkdownEditor,
  },
  data() {
    return {
      model: {
        labels: [],
      },
      categories: [],
      content: '',
      needLoginGithub: [true, false],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date())
            },
          },
          {
            text: '',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            },
          },
          {
            text: '',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            },
          },
        ],
      },
    }
  },
  created() {
    /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.id && this.fetch() //=> this.id true this.fetch
    this.fetchCategories()
  },
  methods: {
    delete_item(index) {
      let arr = this.model.labels
      if (this.model.labels.length > 0) {
        arr.splice(index, 1)
      } else {
        arr = []
      }
      this.model.labels = arr
      // console.log(this.model.labels)
      // this.model.labels.length > 0 &&
      //   (this.model.labels = this.model.labels.splice(index, 1))
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      //=>html:new FormData()
      //=>json
      //=> new FormData()
      const formData = new FormData()
      //append 
      formData.append('file', file)
      //
      const res = await this.$http.post('upload', formData)
      //urlEditor.insertEmbed VueEditor123url
      Editor.insertEmbed(cursorLocation, 'image', res.data.url)
      resetUploader()
    },
    async fetch() {
      //id
      const res = await this.$http.get(`rest/blogs/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = res.data
      console.log(this.model)
    },
    async save() {
      this.model.update_at = Date.now()
      if (this.id) {
        await this.$http.put(`rest/blogs/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/blogs', this.model)
      }

      this.$router.push('/articles/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
    async fetchCategories() {
      //id
      const res = await this.$http.get(`rest/categories`)
      //:  this.model.name = res.data.name 
      this.categories = res.data
    },
  },
}
</script>

<style scoped>
.mt5 {
  margin-bottom: 5px;
}
</style>
