<template>
 <div class="container">
        <h1></h1>
        <el-table :data="heroes">
               <!-- 
                      el:
                      :data for
                      prop
                      scope.row()slot-scope='scope'
                -->
                <el-table-column  prop="_id" label='ID' width="230"></el-table-column>
                <el-table-column  prop="icon" label=''>
                       <!-- 
                              
                               <template slot-scope="scope"></template>
                               scope.row
                        -->
                         <template slot-scope="scope">
                                <img :src="scope.row.avatar" alt="" style='height:3rem'>
                         </template>       
                </el-table-column>
                <el-table-column  prop="title" label=''></el-table-column>
                <el-table-column  prop="name" label=''></el-table-column>
                <el-table-column
                     fixed="right"
                     label=""
                     width="180">
                     <template slot-scope='scope'>
                           <!--  this.$route router this.$router router -->
                           <!-- 
                                  el:
                                  scope.row()
                                  $router.push(url)/
                                  
                            -->
                            <el-button type="primary" size="small" @click='$router.push(`/heroes/edit/${scope.row._id}`)'></el-button>

                             <el-button type="primary" size="small" @click='remove(scope.row)'></el-button>
                     </template>
              </el-table-column>
        </el-table>
 </div>
</template>

<script>
export default {
 data() {
  return {
         heroes:[]
  }
 },
 methods:{
        //
        async fetch(){
             const res = await this.$http.get("rest/heroes")
             this.heroes = res.data
        },
        async remove(row){
          this.$confirm(`${row.name}`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          // 
          await this.$http.delete(`rest/heroes/${row._id}`)
          this.$message({
              type: 'success',
              message: '!'
         });
         //
         this.fetch()
        })
        }
 },
 created(){
        this.fetch()
 },
 components: {

 }
}
</script>

<style scoped lang="scss">

</style>
