<template>
  <div class="container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ id ? '' : '' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <br />
    <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //
    //  id:{type:String}
    id: {},
  },
  data() {
    return {
      model: {},
    }
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    async fetch() {
      //id
      const res = await this.$http.get(`rest/users/${this.id}`)
      //:  this.model.name = res.data.name 
      this.model = res.data
      console.log(this.model)
    },
    async save() {
      if (this.id) {
        await this.$http.put(`rest/users/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/users', this.model)
      }
      this.$router.push('/users/list')
      this.$message({
        type: 'success',
        message: '',
      })
    },
  },
  components: {},
}
</script>

<style scoped lang="scss"></style>
