<template>
 <div class="container">
     <h1>{{id?"":""}}</h1>
     <!-- 
         @submit.native.prevent="save"
         submitsave
         native.preventsubmit
      -->
     <el-form @submit.native.prevent="save" label-width='120px'>
       <!-- 
         el:
         el-tabls -> value="name" el-tab-pane-> name="name"
        -->
         <el-tabs value='information' type='border-card'>
           <el-tab-pane label="" name='information'>
             <el-form-item label=''>
            <el-input v-model='model.name'></el-input>    
         </el-form-item>
          <el-form-item label=''>
            <el-input v-model='model.title'></el-input>    
         </el-form-item>
          <el-form-item label=''>
             <!-- 
                  el:
                  action="" 
                  :on-success 
                  :before-upload 
                  $http.defaults.baseURL //$http.defaults

                  :on-success="res=>model.avatar=res.url"
                  model.avatar
              -->
            <el-upload
            class="avatar-uploader"
            :action="$http.defaults.baseURL+'/upload'"
            :show-file-list="false"
            :headers="getAuthHeaders()"
            :on-success="res=>$set(model,'avatar',res.url)">
            <img v-if="model.avatar" :src="model.avatar" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>   
         </el-form-item> 

           <el-form-item label='Banner'>
            <el-upload
            class="avatar-uploader"
            :action="$http.defaults.baseURL+'/upload'"
            :show-file-list="false"
            :headers="getAuthHeaders()"
            :on-success="res=>$set(model,'banner',res.url)">
            <img v-if="model.banner" :src="model.banner" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>   
         </el-form-item> 
         <el-form-item label=''>
           <!-- 
             el
             multiple 
            -->
           <el-select v-model='model.categories' multiple>
             <el-option v-for="item of categories" :label="item.name" :value="item._id" :key="item._id"></el-option>
           </el-select>
         </el-form-item>
         <el-form-item label=''>
           <!-- 
             model.scores->undefinded
             undefined.defficult->error
             date
            -->
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model='model.scores.difficult'></el-rate>    
         </el-form-item>
          <el-form-item label=''>
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model='model.scores.skills'></el-rate>    
         </el-form-item>
          <el-form-item label=''>
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model='model.scores.attack'></el-rate>    
         </el-form-item>
          <el-form-item label=''>
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model='model.scores.survive'></el-rate>    
         </el-form-item>

         <el-form-item label=''>
           <!-- 
             el
             multiple 
            -->
           <el-select v-model='model.items1' multiple>
             <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
           </el-select>
         </el-form-item>

         <el-form-item label=''>
           <!-- 
             el
             multiple 
            -->
           <el-select v-model='model.items2' multiple>
             <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
           </el-select>
         </el-form-item>
         <el-form-item label="">
            <el-input type='textarea' v-model='model.usageTips'></el-input>
         </el-form-item>
          <el-form-item label="">
            <el-input type='textarea' v-model='model.battleTips'></el-input>
         </el-form-item>
          <el-form-item label="">
            <el-input type='textarea' v-model='model.teamTips'></el-input>
         </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name='skills'>
          <el-button type='default' size='small' @click='model.skills.push({})'><i class="el-icon-plus"></i></el-button>
          <el-row type='flex' style='flex-wrap:wrap'>
              <el-col :md="12" v-for="(item,i) in model.skills" :key='i'>
                <el-form-item label="">
                  <el-input v-model='item.name'></el-input>
                </el-form-item>
                  <el-form-item label="">
                    <!-- 
                       item.icon=res.urlvuedataitemiconitem.iconvue:
                       $set(item,'icon',res.url)
                     -->
                  <el-upload
                  class="avatar-uploader"
                  :action="$http.defaults.baseURL+'/upload'"
                  :show-file-list="false"
                  :headers="getAuthHeaders()"
                  :on-success="res => $set(item,'icon',res.url)">
                  <img v-if="item.icon" :src="item.icon" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </el-upload>  
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model='item.delay'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model='item.cost'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input type='textarea' v-model='item.desciption'></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input type='textarea' v-model='item.tips'></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type='danger' size='small' @click='model.skills.splice(i,1)'></el-button>
                </el-form-item>
              </el-col>
            </el-row>
        </el-tab-pane> 

  <!--  -->
  <el-tab-pane label="" name='partnes'>
          <el-button type='default' size='small' @click='model.partnes.push({})'><i class="el-icon-plus"></i></el-button>
          <el-row type='flex' style='flex-wrap:wrap'>
              <el-col :md="12" v-for="(item,i) in model.partnes" :key='i'>
                <el-form-item label="">
                  <!-- 
                    filterable 
                   -->
                  <el-select filterable v-model='item.hero'>
                    <el-option 
                    :label="hero.name"
                    :value="hero._id"
                    v-for='hero in heroes' :key='hero._id'
                    ></el-option> 
                  </el-select>
                </el-form-item>
                <el-form-item label="">
                  <el-input type='textarea' v-model='item.description'></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type='danger' size='small' @click='model.partnes.splice(i,1)'></el-button>
                </el-form-item>
              </el-col>
            </el-row>
        </el-tab-pane> 


  <!--  -->
         </el-tabs>
         <el-form-item style='margin-top:1rem'>
             <el-button type='primary' native-type='submit'></el-button>
         </el-form-item>
     </el-form>
 </div>
</template>

<script>
export default {
 props:{
     //
     id:{type:String}
 },
 data() {
  return {
      items:[],
      model:{
        skills:[],
        partnes:[],
        name:"",
        heroes:[],
        scores:{
          diffault:0
        },
        avatar:""
      },
      categories:[]
  }
 },
 created(){
     /* 
        true  && false =>false
        false && true => false
        true  || false =>true
        false || true =>true 
     */
    this.fetchHero()
    this.fetchItems()
    this.fetchCategories()
    this.id && this.fetch() //=> this.id true this.fetch
 },
 methods:{
    afterUpload(res){  
      // :this.modelvue vue$set(,key,value)
      // this.model.icon=res.url vue
        this.$set(this.model,"avatar",res.url)
    },
    async fetch(){
      //id
      const res = await this.$http.get(`rest/heroes/${this.id}`);
      //:  this.model.name = res.data.name 
      // this.model = res.data;//=>datacategories
      this.model =  Object.assign({},this.model,res.data)//=>3
     },
     async fetchCategories(){
      //id
      const res = await this.$http.get(`rest/Categories`);
      //:  this.model.name = res.data.name 
      this.categories = res.data; 
     },
     async fetchItems(){
      //id
      const res = await this.$http.get(`rest/items`);
      //:  this.model.name = res.data.name 
      this.items = res.data; 
     },
     async fetchHero(){
       const res = await this.$http.get("rest/heroes")
       this.heroes = res.data
     },
     async save(){
        if(this.id){
          await this.$http.put(`rest/heroes/${this.id}`,this.model)   
        }else{
          await this.$http.post('rest/heroes',this.model)
        }
       this.$router.push("/heroes/list")
       this.$message({
           type:"success",
           message:""
       })
     }
 },
 components: {

 }
}
</script>

<style>

</style>
